import React from "react";

const statusBarStyle = {
  display: "flex",
  position: "fixed",
  bottom: "0%",
  width: "100%",
  padding: "1%",
  margin: "0%",
  marginBottom: ".4%",
  backgroundColor: "rgb(248, 212, 205)",
  zIndex: "1",
};

const barItems = {
  margin: "auto",
  fontWeight: "bold",
  padding: ".1% 3%",
  backgroundColor: "rgb(40, 44, 52)",
  color: "white",
  border: "solid 2px",
  borderRadius: "5%",
  borderColor: "rgb(0, 0, 0)",
  boxShadow: "2px 3px rgb(235, 236, 237)",
};

const StatusBar = ({ setStatus }) => {
  const statusList = ["To Do", "Doing", "Done", "Delayed", "All"];

  return (
    <div className="statusBar" style={statusBarStyle}>
      {statusList.map((i) => (
        <span
          key={i}
          style={barItems}
          onClick={() => {
            setStatus(i);
          }}
        >
          {i}
        </span>
      ))}
    </div>
  );
};

export default StatusBar;
