import React from "react";

const impBarStyle = {
  display: "flex",
  width: "100%",
  margin: "0%",
  marginTop: "1%",
  backgroundColor: "rgb(248, 212, 205)",
  zIndex: "1",
};

const barItems = {
  margin: "auto",
  fontWeight: "bold",
  padding: ".3% 4%",
  backgroundColor: "rgb(40, 44, 52)",
  color: "white",
  border: "solid 2px",
  borderRadius: "8%",
  borderColor: "rgb(0, 0, 0)",
  boxShadow: "2px 3px rgb(235, 236, 237)",
};

const ImpBar = ({ setImportance }) => {
  const impList = ["Not Important", "Important", "Very Important", "All"];

  return (
    <div className="impBar" style={impBarStyle}>
      {impList.map((i) => (
        <span
          key={i}
          style={barItems}
          onClick={() => {
            setImportance(i);
          }}
        >
          {i}
        </span>
      ))}
    </div>
  );
};

export default ImpBar;
